import mpvueCityPicker from '../../components/mpvueCityPicker.vue'

export default {
	components: {
		mpvueCityPicker
	},
	data() {
		return {
			name: '',
			phone: '',
			detail: '',
			cityPickerValueDefault: [0, 0, 1],
			themeColor: '#007AFF',
			pickerText: '请选择'
		}
	},
	methods: {
		// 三级联动选择
		showMulLinkageThreePicker() {
			this.$refs.mpvueCityPicker.show()
		},
		onConfirm(e) {
			this.pickerText = e.label;
		}
	}
}
